import React from 'react';

import './header.scss';
import Lang from '../Lang';
import Menu from '../Menu';

const menu = {
  en: {
    logo: 'CV  Mikhail Zyranau',
    label1: 'About',
    label2: 'Skills',
    label3: 'Portfolio',
  },
  ru: {
    logo: 'CV  Михаил Зырянов',
    label1: 'Общая информация',
    label2: 'Навыки',
    label3: 'Портфолио',
  },
};

const Header = ({ lang, switchLang, switchPage, page }) => {
  const { logo } = menu[lang];
  return (
    <header>
      <h1>{logo}</h1>
      <Menu lang={lang} switchPage={switchPage} page={page} />
      <Lang switchLang={switchLang} />
    </header>
  );
};

export default Header;
